@charset "UTF-8";
.menu__list-link, .rightside-menu__title, .footer__info-title {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700; }

/* переменные должны идти 1 строкой, иначе рип */
.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 120px; }

.header__btn {
  background-color: transparent;
  border: none;
  padding: 0;
  cursor: pointer; }

.logo {
  margin: 0 auto; }

.menu {
  padding-right: 15px; }
  .menu__list {
    display: flex; }
  .menu__list-item {
    padding-right: 45px;
    padding-left: 35px; }
  .menu__list-link {
    color: rgba(54, 56, 56, 0.9033);
    line-height: 22px; }

.rightside-menu {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  max-width: 694px;
  width: 100%;
  background-color: #fff;
  padding-top: 40px;
  box-shadow: -1px 0px 14px 0px #000;
  transition: all 0.5s; }
  .rightside-menu.rightside-menu--close {
    transform: translateX(100%); }
  .rightside-menu__close {
    background-color: transparent;
    border: none;
    padding: 0;
    cursor: pointer;
    margin-left: 50px;
    margin-bottom: 60px;
    height: 20px;
    width: 25px; }
  .rightside-menu__content {
    text-align: center;
    max-width: 485px;
    margin: 0 auto; }
  .rightside-menu__logo {
    margin-bottom: 40px;
    display: inline-block; }
  .rightside-menu__title {
    color: #363838;
    font-size: 36px;
    line-height: 51px;
    position: relative;
    margin-bottom: 17px;
    padding-bottom: 17px; }
    .rightside-menu__title::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      margin: 0 auto;
      max-width: 252px;
      height: 1px;
      background: #6d7278; }
  .rightside-menu__text {
    margin-bottom: 75px; }
  .rightside-menu__images {
    max-width: 485px; }

.footer__content {
  background: #dfdfdf;
  padding: 60px 0 80px; }

.footer__inner {
  display: flex;
  justify-content: space-between; }

.footer__info {
  width: 540px; }

.footer__info-title {
  color: #363838;
  margin-bottom: 14px;
  line-height: 23px; }

.footer__info-text {
  margin-bottom: 16px;
  max-width: 475px; }

.footer__info-email {
  font-size: 14px;
  color: #363838;
  font-weight: 700;
  padding-left: 30px;
  margin-bottom: 22px;
  display: inline-block;
  position: relative; }
  .footer__info-email::before {
    content: "";
    left: 0;
    top: 9px;
    width: 16px;
    height: 12px;
    position: absolute;
    background-repeat: no-repeat;
    background-image: url(../images/mail.svg); }

.footer__form {
  display: flex; }

.footer__form-input {
  padding: 6px 10px 6px 18px;
  border-radius: 21px;
  background-color: transparent;
  border: 1px solid rgba(54, 56, 56, 0.7);
  outline: none;
  width: 100%; }
  .footer__form-input::placeholder {
    font-size: 16px;
    line-height: 27;
    color: #a1a1a1; }

.footer__form-button {
  background-color: #363838;
  color: #ffff;
  border: none;
  border-radius: 21px;
  font-weight: 400;
  padding: 6px 12px;
  min-width: 160px;
  min-height: 39px;
  margin-left: 9px;
  cursor: pointer; }

.footer__social {
  width: 125px;
  margin-right: auto;
  margin-left: 100px; }

.footer__social-link {
  padding-left: 28px;
  position: relative; }
  .footer__social-link::before {
    top: 3px;
    content: "";
    position: absolute;
    left: 0;
    width: 16px;
    height: 16px;
    background-repeat: no-repeat; }

.footer__social-link--facebook::before {
  background-image: url(../images/facebook.svg);
  background-repeat: no-repeat; }

.footer__social-link--instagram::before {
  background-image: url(../images/instagram.svg);
  background-repeat: no-repeat;
  width: 14px;
  height: 14px;
  top: 4px; }

.footer__social-link--pinterest::before {
  background-image: url(../images/pinterest.svg);
  background-repeat: no-repeat; }

.footer__social-link--whatsapp::before {
  background-image: url(../images/whatsapp.svg);
  background-repeat: no-repeat;
  width: 14px;
  height: 14px; }

.footer__social-link--youtube::before {
  background-image: url(../images/youtube.svg);
  background-repeat: no-repeat;
  top: 4px;
  width: 18px;
  height: 12px; }

.footer__social-item, .footer__menu-item {
  margin-bottom: 15px;
  font-weight: 600;
  font-size: 14px;
  color: #707070;
  font-family: "Open Sans", sans-serif;
  font-style: normal; }

.footer__social-link, .footer__menu-link {
  color: #707070; }

.footer__menu {
  margin-right: 60px;
  width: 125px; }

.footer__copyright {
  background-color: #363838;
  color: #a1a1a1;
  text-align: center;
  padding: 42px 0 41px; }

html,
body {
  height: 100%; }

body {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 27px;
  color: #5A5A5A;
  overflow-x: hidden;
  min-width: 320px; }

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%; }

.main {
  flex: 1 0 auto; }

.footer {
  flex: 0 0 auto; }

.container {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px; }

html {
  box-sizing: border-box; }

*,
*::after,
*::before {
  box-sizing: inherit; }

ul[class],
ol[class] {
  padding: 0; }

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  font-size: inherit; }

ul[class] {
  list-style: none; }

img {
  max-width: 100%;
  display: block; }

input,
button,
textarea,
select {
  font: inherit; }

a {
  text-decoration: none; }
